.light {
  --mainColor: #64bcf4;
  --hoverColor: #5bacdf;
  --backgroundColor: #f1f8fc;
  --darkOne: #312f3a;
  --darkTwo: #45424b;
  --lightOne: #919191;
  --lightTwo: #aaa;
}

.dark {
  --mainColor: #64bcf4;
  --hoverColor: #5bacdf;
  --backgroundColor: #192e3a;
  --darkOne: #f3f3f3;
  --darkTwo: #fff;
  --lightOne: #ccc;
  --lightTwo: #e7e3e3;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  /* background-color: #001d31; */
}

.stop-scrolling {
  height: 100%;
  overflow: hidden;
}

img {
  width: 100%;
}

a {
  text-decoration: none;
}

.big-wrapper {
  position: relative;
  padding: 1.7rem 0 2rem;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  background-color: var(--backgroundColor);
  /* background-color: #001d31; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  position: relative;
  /* flex: 1; */
  max-width: 81rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 3rem;
  z-index: 10;
}

.loader-landing {
  width: 100%;
  background-color: #001d31;
}

.img-loader {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-left: auto;
  width: 50%;

  /* top:auto; 
 right: 0; 
 width: 30px; 
 height: 30px;
 background-color: rgba(255, 255, 255, .5) */
